<template>
  <div>
    <LoginComponent v-if="!token" />
    <h1>Latest Posts</h1>
    <div v-if="token" class="create-post">
      <label for="create-post">Post something... </label>
      <input type="text" id="create-post" v-model="text" v-on:keyup.enter="createPost" placeholder="Make a post">
      <button v-on:click="createPost">Submit</button>
    </div>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
      <div class="posts-container">
        <div
          v-for="(post, index) in posts"
          v-bind:item="post"
          v-bind:index="index"
          v-bind:key="post._id"
        >
          {{ `${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()} ${post.createdAt.getHours()}:${post.createdAt.getMinutes()}` }}
          <p>{{ post.text }}</p>
          <button v-if="token" v-on:click="deletePost(post._id)">Delete post</button>
        </div>
      </div>

  </div>
</template>

<script>
import PostService from '../PostService';
import LoginComponent from '@/components/LoginComponent.vue'


export default {
  name: 'PostComponent',
  components: {
    LoginComponent
  },
  data() {
    return {
      posts: [],
      error: '',
      text: '',
    }
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    async createPost() {
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts();
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    }
  }
}
</script>

