import axios from 'axios';
const url = 'api/users/';

class AuthService {
    static getToken(user, pass) {
        return new Promise ((resolve, reject) => {
            axios.post(url, {user: user, password: pass}).then((res) => {
                return res.data;
            })
            .catch((err)=> {
                reject(err);
            })
        });
    }
}

export default AuthService;